$silver: rgba(255, 255, 255, .5);
$bright-orange: #edae3b;
$bright-red: #FE2E2E;

.wrap {
  max-width: 1300px;
  width: 90%;
  margin: 0 auto;
}
.section {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: flex-start;
  padding-bottom: 30px;
  gap: 30px;
  .svg svg {
    width: 80px;
    height: 80px;
    padding-top: 30px;
  }
  .contacts, .mode, .social {
    span {
      font-weight: 700;
      font-size: 24px;
      line-height: 29px;
    }
    ul {
      margin-top: 20px;
    }
    ul li {
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      a {
        text-decoration: none;
      }
    }
  }
  .contacts {
    text-align: center;
  }
  .mode {
    ul {
      margin-top: 20px;
    }
  }
  .social {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    svg {
      margin-top: 20px;
      &:hover {
        stroke: $bright-orange;
        fill: $bright-orange;
      }
    }
  }
}
.border {
  border-bottom: 2px solid $bright-red;
  border-top: 2px solid $bright-red;
  max-width: 1300px;
  width: 100%;
  padding-top: 20px;
}
.copyright {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px 0;
  span {
    font-weight: 700;
    font-size: 16px;
    font-style: italic;
  }
}
@media screen and (max-width: 720px) {
  .section {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 60px;
  }
}